$loaderBackground: #333333;

// I use spinner from https://projects.lukehaas.me/css-loaders/ site
.loader-container {
    width: 100vw;
    .loader {
        font-size: 10px;
        margin: 150px auto;
        text-indent: -9999em;
        width: 11em;
        height: 11em;
        border-radius: 50%;
        background: $loaderBackground;
        background: -moz-linear-gradient(left, $loaderBackground 10%, rgba(255, 255, 255, 0) 42%);
        background: -webkit-linear-gradient(left, $loaderBackground 10%, rgba(255, 255, 255, 0) 42%);
        background: -o-linear-gradient(left, $loaderBackground 10%, rgba(255, 255, 255, 0) 42%);
        background: -ms-linear-gradient(left, $loaderBackground 10%, rgba(255, 255, 255, 0) 42%);
        background: linear-gradient(to right, $loaderBackground 10%, rgba(255, 255, 255, 0) 42%);
        position: relative;
        -webkit-animation: load3 1.4s infinite linear;
        animation: load3 1.4s infinite linear;
        -webkit-transform: translateZ(0);
        -ms-transform: translateZ(0);
        transform: translateZ(0);

        &:before {
            width: 50%;
            height: 50%;
            background: $loaderBackground;
            border-radius: 100% 0 0 0;
            position: absolute;
            top: 0;
            left: 0;
            content: ''; }

        &:after {
            background: #b20000;
            width: 75%;
            height: 75%;
            border-radius: 50%;
            content: '';
            margin: auto;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0; } } }

@-webkit-keyframes load3 {
  0% {
            -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }

  100% {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes load3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg); }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg); } }
