@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@300;500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@0,100;0,400;0,500;0,700;0,900;1,100;1,200;1,300&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loader-container{width:100vw}.loader-container .loader{font-size:10px;margin:150px auto;text-indent:-9999em;width:11em;height:11em;border-radius:50%;background:#333;background:linear-gradient(to right, #333 10%, rgba(255,255,255,0) 42%);position:relative;animation:load3 1.4s infinite linear;transform:translateZ(0)}.loader-container .loader:before{width:50%;height:50%;background:#333;border-radius:100% 0 0 0;position:absolute;top:0;left:0;content:''}.loader-container .loader:after{background:#b20000;width:75%;height:75%;border-radius:50%;content:'';margin:auto;position:absolute;top:0;left:0;bottom:0;right:0}@keyframes load3{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.articles{margin:0 auto;display:flex;flex-direction:column;align-items:center;background-image:linear-gradient(#ddd, #bbb);transition:1s}.articles .articles-container{width:65%;margin-top:-300px;display:grid;grid-template-columns:300px 300px 300px;grid-template-rows:auto;justify-content:space-between;margin-bottom:100px;grid-row-gap:50px;row-gap:50px}.articles .articles-container .article-link{text-decoration:none;color:#777777;width:300px;height:600px;animation:enlarge-new-item 300ms ease-in-out 0s 1 normal forwards;box-sizing:border-box}.articles .articles-container .article-link .article{margin:0 auto;background-color:#ffffff;max-width:300px;height:600px;display:flex;flex-direction:column;justify-content:space-between;transform:scale(1);transition:transform 0.2s ease-in-out}.articles .articles-container .article-link .article .article-image-div{position:relative;max-width:300px;height:300px;overflow:hidden}.articles .articles-container .article-link .article .article-image-div .article-image{position:absolute;min-height:300px;height:100%;left:50%;top:50%;transform:translateX(-50%) translateY(-50%);opacity:0.9}.articles .articles-container .article-link .article .text-container{padding:20px 25px 40px 25px;height:300px;display:flex;flex-direction:column;justify-content:space-between;position:relative}.articles .articles-container .article-link .article .text-container .article-title{font-size:20px}.articles .articles-container .article-link .article .text-container .article-excerpt{font-style:italic;font-weight:400;font-size:14px}.articles .articles-container .article-link .article .text-container .article-arrow{position:absolute;width:40px;height:40px;transform:rotate(45deg);bottom:-20px;background:white;z-index:1}.articles .articles-container .article-link .article:hover{color:#333333;box-shadow:5px 5px 10px 5px rgba(0,0,0,0.05);border:1px solid #333333;transform:scale(1.05)}.articles .articles-container .article-link .article:hover .article .article-image-div img{opacity:1}@keyframes enlarge-new-item{from{transform:scale(0)}to{transform:scale(1)}}@media (max-width: 1500px){.articles .articles-container{width:85%}}@media (max-width: 1050px){.articles .articles-container{width:100%;grid-template-columns:300px 300px;justify-content:space-around}.articles .articles-container .article-link .article:hover{transform:scale(1)}}@media (max-width: 650px){.articles .articles-container{grid-template-columns:300px;justify-content:center}.articles .articles-container .article-link .article:hover{transform:scale(1)}}

.Header{width:100vw;height:80vh;background-image:url(/oko-articles-react/static/media/reader.8b6ca600.jpg);background-size:cover;background-position-y:50%}.Header .header-texts{width:65vw;margin:0 auto;padding-top:100px;color:#ffffff}.Header .header-texts h1{font-size:100px;margin:0}.Header .header-texts h2{font-size:35px;margin:0}@media (max-width: 1500px){.Header .header-texts{width:85%}.Header .header-texts h1{font-size:90px;margin:0}.Header .header-texts h2{font-size:35px;margin:0}}@media (max-width: 1050px){.Header .header-texts{width:90%}.Header .header-texts h1{font-size:80px;margin:0}.Header .header-texts h2{font-size:45px;margin:0}}@media (max-width: 650px){.Header .header-texts{width:90%;padding-top:50px}.Header .header-texts h1{font-size:70px;margin:0}.Header .header-texts h2{font-size:25px;margin:0}}@media (max-width: 400px){.Header .header-texts{width:90%;padding-top:40px}.Header .header-texts h1{font-size:50px;margin:0}.Header .header-texts h2{font-size:25px;margin:0}}

html{box-sizing:border-box;overflow-x:hidden}*,*:before,*:after{margin:0;padding:0;box-sizing:inherit;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.App{font-family:'Piazzolla', serif;width:100%;display:flex;flex-direction:column;align-items:center;overflow-x:hidden}

