.articles {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: linear-gradient( #dddddd, #bbbbbb);
    transition: 1s;
    .articles-container {
        width: 65%;
        margin-top: -300px;
        display: grid;
        grid-template-columns: 300px 300px 300px;
        grid-template-rows: auto;
        justify-content: space-between;
        margin-bottom: 100px;
        row-gap: 50px;
        .article-link {
            text-decoration: none;
            color: #777777;
            width: 300px;
            height: 600px;
            animation: enlarge-new-item 300ms ease-in-out 0s 1 normal forwards;
            box-sizing: border-box;
            .article {
                margin: 0 auto;
                background-color: #ffffff;
                max-width: 300px;
                height: 600px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                transform: scale(1);
                transition: transform 0.2s ease-in-out;
                .article-image-div {
                    position: relative;
                    max-width: 300px;
                    height: 300px;
                    overflow: hidden;
                    .article-image {
                        position: absolute;
                        min-height: 300px;
                        height: 100%;
                        left: 50%;
                        top: 50%;
                        transform: translateX(-50%) translateY(-50%);
                        opacity: 0.9; } }
                .text-container {
                    padding: 20px 25px 40px 25px;
                    height: 300px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    position: relative;
                    .article-title {
                        font-size: 20px; }
                    .article-excerpt {
                        font-style: italic;
                        font-weight: 400;
                        font-size: 14px; }
                    .article-arrow {
                        position: absolute;
                        width: 40px;
                        height: 40px;
                        transform: rotate(45deg);
                        bottom: -20px;
                        background: white;
                        z-index: 1; } }
                &:hover {
                    color: #333333;
                    box-shadow: 5px 5px 10px 5px rgba(0,0,0,0.05);
                    border: 1px solid #333333;
                    transform: scale(1.05);
                    .article {
                        .article-image-div {
                            img {
                                opacity: 1; } } } } } } } }

@keyframes enlarge-new-item {
    from {
        transform: scale(0); }

    to {
        transform: scale(1); } }

@media(max-width: 1500px) {
    .articles {
        .articles-container {
            width: 85%; } } }

@media(max-width: 1050px) {
    .articles {
        .articles-container {
            width: 100%;
            grid-template-columns: 300px 300px;
            justify-content: space-around;
            .article-link {
                .article {
                    &:hover {
                        transform: scale(1); } } } } } }

@media(max-width: 650px) {
    .articles {
        .articles-container {
            grid-template-columns: 300px;
            justify-content: center;
            .article-link {
                .article {
                    &:hover {
                        transform: scale(1); } } } } } }
